.dialog {
    margin: 100px auto;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: -100px;
    left: 0;
    border: none;
    padding-top: 150px;
    box-sizing: border-box;
  }
  .dialog input {
    display: block;
    margin: 20px auto;
    padding: 10px;
    width: 20%;
    min-width: 200px;
    box-sizing: border-box;
  }