.answer {
    color: blue;
    cursor: pointer;
    font-weight: 500;
}
.answer:hover {
    font-weight: 600;
}
.answered textarea {
    display: block;
    width: 100%;
    margin: 10px 0 0;
    padding: 10px;
    font-size: 16px;
    outline: none;
}
.answered {
    max-width: 400px;
    min-width: 100%;
}
.out_message {
    padding: 10px;
}
.answered button:first-of-type {
    float: left;
    margin: 20px 0;
}
.answered button:last-of-type {
    float: right;
    margin: 20px 0;
}
.answered h2 {
    margin: 0;
}
.recive_message {
    cursor: pointer;
    color: green;
    display: inline-block;
    vertical-align: middle;
}
.icon span, .icon a {
    font-size: 30px;    
}
.icon {
    flex-direction: row;
}
.data {
    min-width: 700px;
}