.review {
    position: relative;
    width: 30%;
    padding-left: 10px;
    text-align: left;
    font-size: 14px;
    padding-right: 20px;
}
.review::after {
    content: '\274C';
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 5px;
}
.delete__master {
    font-size: 20px;
    color: red;
    cursor: pointer;
    transition: all 0.5s;
}
.delete__master:hover {
    transform: scale(1.1);
}