.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}



.App-header {
 
  min-height: 5vh;
  display: flex;
  padding: 5px 0;
 
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
 
}

.App-header button, .App-header a {
  width: 20%;
  min-width: fit-content;
  padding: 0 20px;
  border: none;
  border-radius: 5px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  cursor: pointer;
  line-height: 34px;
  font-size: clamp(16px, 1vw, 22px);
  background-color: inherit;
}


.client {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ddd;
  align-items: center;
  margin: 0;
  padding: 10px 0;
}

.header {
  padding: 10px 0;
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  background-color: #ccc;
}
.header .прайс {
  width: 10%;
}

.client span {
  display: inline-block;
  width: 25%;
}



.data_wrap {
  width: 100%;
  overflow: auto;
}

.data {
  min-width: 500px;
}

.dialog__bloking {
  position: fixed;
  border: none;
  width: 300px;
  top: 200px;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px #C5C5C5;
  border-radius: 12px;
  padding: 20px 0;

}
.dialog__bloking button {
  width: 100px;
  margin: 20px 20px 0;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  padding: 5px;
  color: #FFFFFF;
  background-color: #61dafb;
}
.dialog__bloking button:hover {
  box-shadow: 0 10px 20px #C5C5C5;
}
.dialog__bloking button:focus {
  box-shadow: 0px 0px 5px #C5C5C5;
}
.dialog__bloking p {
  margin: 10px 0 0;
  font-weight: 500;
}
.dialog__bloking button:last-of-type {
  background-color: red;
}

@media screen and (max-width: 480px) {
  body {
    background-color: lightgreen;
  }

  .App-header {
    flex-direction: column;
    height: auto;
    align-items: center;
  }

  .App-header button, .App-header a {
   
   margin: 5px 0;
    
  }

  .client span:first-of-type {
    padding-left: 8px;
  }

  .client {
    font-size: 14px;
    align-items: center;
  }
}